.menu-item {
	min-width: 30%;
	height: 240px;
	flex: 1 1 auto;
	display: flex;
	align-items: center;
	justify-content: center;
	border: 3px solid black;
	margin: 0 7.5px 15px;
	background-position: center;
	background-size: cover;
	border-radius: 10px;
	overflow: hidden;

	&:hover {
		cursor: pointer;

		& .background-image {
			transform: scale(1.1);
			transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
		}

		& .content {
			opacity: 0.9;
		}
	}

	&.large {
		height: 380px;
	}

	&:first-child {
		margin-right: 7.5px;
	}

	&:last-child {
		margin-left: 7.5px
	}

	.background-image {
		width: 100%;
		height: 100%;
		background-size: cover;
		background-position: center;
	}

	.content {
		height: 60px;
		padding: 0 25px;
		display: flex;
		flex-direction: column;
		align-items: centers;
		justify-content: center;
		border-radius: 25px;
		border: 2px solid black;
		background-color: rgba(0,0,55,.6);
		position: absolute;
	}

	.title {
		font-weight: bold;
		font-size: 26px;
		color: white;
	}

	.subtitle {
		font-weight: lighter;
		font-size: 16px;
	}
}